.header {
  z-index: 1000;
  transition: all .3s;
  position: relative;
}

.header__container {
  max-width: 1200px;
  margin: 0 auto;
}

.header.header--fixed {
  background-color: #eef7ff;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 8px #0000001a;
}

.header__container {
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 20px;
  padding: 10px 40px;
  display: grid;
}

.header__burger {
  cursor: pointer;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: space-between;
  justify-self: end;
  width: 24px;
  height: 24px;
  padding: 0;
  display: none;
}

.header__burger span {
  background-color: #f9512e;
  width: 20px;
  height: 2px;
  transition: all .3s;
}

.header__burger span:first-child {
  transform: translateY(8px);
}

.header__burger span:last-child {
  transform: translateY(-8px);
}

.header__burger.active span:first-child {
  transform: rotate(45deg)translate(10px, 11px);
}

.header__burger.active span:last-child {
  transform: rotate(-45deg)translate(5px, -5px);
}

.header__right {
  justify-content: flex-end;
  align-items: center;
  gap: 40px;
  transition: all .3s;
  display: flex;
}

@media (width <= 768px) {
  .header__right {
    opacity: 0;
    pointer-events: none;
    background-color: #eef7ff;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    width: auto;
    padding: 20px;
    transition: transform .3s, opacity .3s;
    position: absolute;
    top: 51px;
    left: 0;
    right: 0;
    transform: translateX(100%);
    box-shadow: 0 2px 8px #00000026;
  }

  .header__right.header__right--active {
    opacity: 1;
    pointer-events: auto;
    transform: translateX(0);
  }
}

